// src/pages/apps/Business/BusinessDetails.tsx
import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

interface Outlet {
    outlet_id: string;
    outlet_name: string;
    outlet_address: string;
}

interface Business {
    business_name: string;
    business_logo: string;
    business_address: string;
    business_contact: string;
    cuisine: string;
    gst_no: string;
    outlets: Outlet[];
}

const BusinessDetails = () => {
    const { businessId } = useParams<{ businessId: string }>(); // Extract businessId from URL params
    const [business, setBusiness] = useState<Business | null>(null);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchBusinessDetails = async () => {
            try {
                const res = await fetch(`https://dncdev.infocorptus.com/api/v1/Business/${businessId}`);
                if (!res.ok) {
                    throw new Error('Failed to fetch business details');
                }
                const data: Business = await res.json();
                setBusiness(data);
            } catch (error) {
                console.error(error);
                setError('Failed to load business details');
            }
        };

        fetchBusinessDetails();
    }, [businessId]); // Run this effect when businessId changes

    if (error) {
        return <div>{error}</div>;
    }

    return <div>Loading business details...</div>;
};

export default BusinessDetails;
